.myButton {
  min-width: 65px;
  background-color: rgb(255, 96, 96);
  border: none;
  padding: 6px 10px;
  margin: 5px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.primary {
  background-color: rgb(96, 175, 255);
}

.myButton:hover {
  background-color: #6f8fac;
}
